








































import Vue from 'vue';
import { mapGetters } from 'vuex';
import * as moment from 'moment';

export default Vue.extend({
  name: 'overview',

  data() {
    return {
      dateToday: '',
      dateDue: ''
    };
  },
  computed: {
    ...mapGetters([
      'total'
    ])
  },

  created() {
    this.dateToday = moment().format('MMMM D, YYYY');
    this.dateDue = moment().add(1, 'month').format('MMMM D, YYYY');
  }
});
